button {
  width: 8rem;
  height: 3rem;
  padding: 0.5rem;
  border: none;
  color: #cccccc; }
  button.dark {
    background-color: #333333; }
    button.dark:hover {
      background-color: #4d4d4d;
      color: #fff; }
    button.dark:focus {
      outline: none; }

.container {
  width: 100%;
  height: 100vh;
  padding: 20px 20px 40px 20px;
  display: flex;
  align-items: center;
  justify-content: center; }

.sidenav {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: #111;
  overflow-x: hidden;
  padding-top: 60px;
  transition: 0.5s; }
  .sidenav a,
  .sidenav button {
    padding: 8px 8px 8px 32px;
    text-align: center;
    text-decoration: none;
    font-size: 25px;
    color: #818181;
    display: block;
    transition: 0.3s; }
  .sidenav :hover,
  .sidenav .offcanvas,
  .sidenav :focus {
    color: #f1f1f1;
    outline: 0; }
  .sidenav .closebtn {
    position: absolute;
    border: none;
    background-color: #111;
    top: 0;
    right: 25px;
    font-size: 36px;
    margin-left: 50px; }

.openbtn {
  position: fixed;
  border: none;
  background-color: #111;
  top: 0;
  left: 0; }



